.NavBar {
    background: #7776bb;
    height: 60px;
    line-height: 60px;
    display: flex;
    justify-content: flex-end;
    color: aliceblue;
}

.blog-title {
    flex-grow: 1;
    font-size: 30px;
    justify-content: center;
    display: flex;
    font-weight: 700;
}

