/*}*/

.SubNav {
    flex-grow: 2;
    display: flex;
    justify-content: center;
}

.SubNav div {
    flex-grow: 1;
    text-align: center;
    font-size: 16px;
}

.SubNav div a {
    display: block;
    color: white;
    text-decoration: none;
}

.SubNav div:hover {
    background-color: #7776d0;
    border-bottom: 4px solid #13daec;
}

